import React, { useState, useEffect, useContext, useRef } from 'react';
import { CircuitContext } from '../context/CircuitContext';
import CircuitCheckbox from './CircuitCheckbox';
import CircuitTimer from '../utils/CircuitTimer';
import CircuitTimerGPT from './CircuitTimerGPT';
import getKey from './KeyGenerator';
import VideoDetail from './VideoDetail';
import YouTubePlayer from './YouTubePlayer';
import Breathing from './Breathing';
import BreathingPath from './BreathingPath';
import Sounds from './Sounds';

const CircuitContainer = ({
    index,
    circuit,
    goal,
    category,
    circuitIndex,
    circuitGroupIndex,
    editCircuit,
    toggleCheckbox,
    deleteGoal
}) => {

    const {
        circuits,
        setCircuits,
        activeIndex,
        setActiveIndex,
        nextActiveIndex,
        setNextActiveIndex,
        activated,
        setActivated,
        scrollToElement,
        videoId,
        setVideoId,
        videoActive,
        setVideoActive,
        dimensions, 
        setDimensions
    } = useContext(CircuitContext);

    const videoPlayer1Ref = useRef(null);
    const [active, setActive] = useState(false);
    const [isUpNext, setIsUpNext] = useState(false);
    const chillComplete = (index) => console.log(`${circuits[circuitGroupIndex].circuits[circuitIndex].excersizes[index].title} chill complete`);
    const [chilling, setChilling] = useState(false);
    const getVideoID = () => {
        if (goal.link.includes('/shorts/')) {
            return goal.link.split('/shorts/')[1];
        } else {
            return goal.link.split('=')[1];
        }
    }
    useEffect(() => {
        console.log(`CircuitContainer => ${goal.title} = chilling: ${chilling}`);
    }, [chilling]);

    useEffect(() => {
        if (activeIndex === `sessionindex${index}groupIndex${circuitGroupIndex}subgroupIndex${circuitIndex}`) {
            console.log(`CircuitContainer => ${goal.title} = activeIndex: ${activeIndex}`);
            setActive(true);
            if (goal.link.includes('youtu')) {
                setVideoId(getVideoID());
                setVideoActive(true);
            }
        } else {
            setActive(false);
        }
    }, [activeIndex]);
    useEffect(() => {
        if (nextActiveIndex === `sessionindex${index}groupIndex${circuitGroupIndex}subgroupIndex${circuitIndex}`) {
            console.log(`CircuitContainer => ${goal.title} = nextActiveIndex: ${nextActiveIndex}`);
            setIsUpNext(true);
        } else {
            setIsUpNext(false);
        }
    }, [nextActiveIndex]);
    const triggerChill = (index, groupIndex, subgroupIndex) => {
        const nextIndex = `chillindex${Number(index)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`;
        setChilling(true);
        console.log(`triggerChill => ${goal.title} chilling: ${chilling} ${nextIndex}`)
        setActiveIndex(nextIndex);
        setVideoActive(false);
        if (activeIndex !== null) {
            scrollToElement(activeIndex);
        }
    }
    const triggerTimer = (index, groupIndex, subgroupIndex) => {
        const nextIndex = `sessionindex${Number(index + 1)}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`;
        setChilling(false);
        console.log(`triggerTimer => ${goal.title} chilling: ${chilling} ${nextIndex}`)
        setActiveIndex(nextIndex);
        if (activeIndex !== null) {
            scrollToElement(activeIndex);
        }
    }

    const sessionTimer = (index, goal) => {
    return (
        <div>
            {
                (active && goal.link.includes('youtu'))
                ? (goal.link.includes('youtu'))
                    ? <iframe title='videoPlayer' width={dimensions.width} height={dimensions.height} frameBorder='0' className='r-5' allowFullScreen='' src={`${videoSource}&autoplay=1`}></iframe>
                    //?<YouTubePlayer videoId={getVideoID()} />
                    //? <VideoDetail video={getVideoDetail()} />
                        //? <video controls>
                        //    <source src={goal.link} type="video/mp4" />
                        //    Your browser does not support the video tag.
                        //</video>
                    : <div className='mt-20'>
                        <img className='width-100-percent button' src={goal.link} alt={goal.title} />
                    </div>
                : null
            }
            <CircuitTimer
                category='session'
                excersize={goal}
                index={index}
                groupIndex={circuitGroupIndex}
                subgroupIndex={circuitIndex}
                circuits={circuits}
                time={Number(circuits[circuitGroupIndex].circuits[circuitIndex].time) ?? 5}
                toggleCheckbox={toggleCheckbox}
                triggerNextTimer={triggerChill}
            />
        </div>
    )
}
                
    const chillTimer = (index, goal) => {
        return (
            <div>
                {
                    (activeIndex === `chillindex${Number(index)}groupIndex${circuitGroupIndex}subgroupIndex${circuitIndex}`)
                    ? <div className='containerBox'>
                        <div className='m-auto'>
                            <BreathingPath />
                        </div>
                        <div className='ml-50Percent mt--180'>
                            <Breathing />
                        </div>
                    </div>
                    :null
                }
                <CircuitTimer
                    category='chill'
                    excersize={goal}
                    index={index}
                    groupIndex={circuitGroupIndex}
                    subgroupIndex={circuitIndex}
                    time={circuit.restTime}
                    toggleCheckbox={chillComplete}
                    triggerNextTimer={triggerTimer}
                />
            </div>
        )
    }
    
    
    const handleVideoUpdate = (event) => {
        const { currentTime } = event.target;
        console.log(`currentTime: ${currentTime}`)
    };
    const getVideoDetail = () => {
        scrollToElement(activeIndex);
        return {
            kind: "youtube#searchResult",
            etag: "yuJ8sfOwcgHU2dHV22aAbrF-5RY",
            id: {
                kind: "youtube#video",
                videoId: getVideoID()
            },
            snippet: {
                publishedAt: "2024-02-11T23:48:07Z",
                channelId: "UCTAkZ_TUCLSRjzGa7FoBK5Q",
                title: goal.title,
                description: circuits[circuitGroupIndex].description,
                thumbnails: {
                    default: {
                        url: "https://i.ytimg.com/vi/F2njH0EXhSU/default.jpg",
                        width: 120,
                        height: 90
                    },
                    medium: {
                        url: "https://i.ytimg.com/vi/F2njH0EXhSU/mqdefault.jpg",
                        width: 320,
                        height: 180
                    },
                    high: {
                        url: "https://i.ytimg.com/vi/F2njH0EXhSU/hqdefault.jpg",
                        width: 480,
                        height: 360
                    }
                },
                channelTitle: circuits[circuitGroupIndex].title,
                liveBroadcastContent: "none",
                publishTime: "2024-02-11T23:48:07Z"
            }
        }
    }
    const videoSource = `https://www.youtube.com/embed/${getVideoID()}?autoplay=1`;
    return <div
        key={getKey(`goal${index}`)}
        className={`containerBox centerVertical ${(active) ? 'bg-dark' : ''}`}
    >
        <CircuitCheckbox
            index={index}
            goal={goal}
            category={category}
            circuitIndex={circuitIndex}
            circuitGroupIndex={circuitGroupIndex}
            editCircuit={editCircuit}
            toggleCheckbox={toggleCheckbox}
            deleteGoal={deleteGoal}
        />
        {sessionTimer(index, goal)}
        {chillTimer(index, goal)}
    </div>
}
export default CircuitContainer;