import React, { useState, useEffect } from 'react';
import Sounds from '../utils/Sounds';

const CountdownTimer = ({
  time,
  setTimesUp,
  recordHeatScores
}) => {

  const localTimeLeft = Number(localStorage.getItem('timeLeft'))
  const initTimeLeft = (Number(localStorage.getItem('timeLeft'))<0)?Number(localStorage.getItem('time')):Number(localStorage.getItem('timeLeft')) || time;

  const [pause, setPause] = useState((localStorage.getItem('timerPause') === 'true') ? true : false);
  const [startTime, setStartTime] = useState(parseInt(localStorage.getItem('timerStartTime')) || 0);
  const [timeLeft, setTimeLeft] = useState();

  const isStart = () => (timeLeft == time) ? true : false;
  const remainingSeconds = (milliseconds) => Math.floor(milliseconds / 1000);

  const calculateTimeLeft = () => {
    const millisecondsRemaining = timeLeft * 1000;
    const elapsedMilliseconds =  Number(Date.now() - startTime);
    const remainingMilliseconds = Number(millisecondsRemaining - elapsedMilliseconds);
    const timeRemaining = remainingSeconds(remainingMilliseconds);
    
    const secondsRemaining = (timeRemaining > -1) ? timeRemaining : 0;

    if (secondsRemaining <= 10 && secondsRemaining > 0 ) {
      Sounds.boop(0, secondsRemaining);
    }
    if (timeRemaining <= 0) {
        Sounds.boop(0, secondsRemaining);
        setTimesUp(true);
        recordHeatScores();
        setPause(true);
        setTimeLeft(secondsRemaining);
    }
    setTimeLeft(timeRemaining);
  };

  const startTimer = () => {
    setStartTime(Date.now());
    console.log(`timeLeft: ${Number(localStorage.getItem('timeLeft'))}`)
    setPause(false);
  };

  const pauseTimer = () => {
    setPause(true);
    //localStorage.setItem('timeLeft', timeLeft)
  };

  const resetTimer = () => {
    setPause(true);
    setStartTime(0);
    setTimeLeft(time);
    setTimesUp(false)
  };
  
  useEffect(() => {
    if (timeLeft > 0) {
      localStorage.setItem('timeLeft', timeLeft);
    }
  }, [timeLeft]);

  useEffect(() => {
    let timerInterval;

    if (!pause) {
      calculateTimeLeft();
      timerInterval = setInterval(() => {
        calculateTimeLeft();
      }, 1000);
    }

    return () => clearInterval(timerInterval);
  }, [pause, startTime]);

  useEffect(() => {
    //alert(`pause: ${pause}`);
    localStorage.setItem('timerPause', pause);
  }, [pause]);
  useEffect(() => {
    localStorage.setItem('timerStartTime', startTime);
    //localStorage.setItem('timeLeft', timeLeft);
  }, [startTime]);
  /*
    useEffect(() => {
      window.addEventListener('beforeunload', calculateTimeLeft);
      return () => {
        window.removeEventListener('beforeunload', calculateTimeLeft);
      };
    }, []);
  */
  useEffect(() => {
    const localTime = Number(localStorage.getItem('time'));
    if (isStart() || (time !== localTime)) {
      console.log(`time: ${time} localTime: ${localTime}`)
      setTimeLeft(time);
      resetTimer()
    }
    //console.log(`CountdownTimer: timeLeft: ${timeLeft} time: ${time}`)
    //setPause(true);
    //console.log(`ContdownTimer => time: ${time}`);
    localStorage.setItem('time', time);
  }, [time]);

  useEffect(() => {
    localStorage.setItem('time', time);
    const timerStatus = (localStorage.getItem('timerPause') === 'true') ? true : false;
    let timerTimeLeft = Number(localStorage.getItem('timeLeft')) || time;
    if (timerTimeLeft < 0) {
      timerTimeLeft = 0;
    }
    //setPause(false)

    const timerStart = () => {
      if (localStorage.getItem('timerStartTime')) {
        setTimeLeft(timerTimeLeft);
        return localStorage.getItem('timerStartTime')
      }
      return null
    }
    setStartTime(timerStart());

    //console.log(`CountdownTimer: timeLeft: ${timeLeft} time: ${time}`)
    if (timeLeft == time) {
      //setPause(true);
    }
    //console.log(`ContdownTimer => time: ${time}`);
  }, []);

  const formatTime = (seconds) => {
    const hoursDisplay = Math.floor(seconds / 3600);
    const minutesDisplay = Math.floor((seconds % 3600) / 60);
    const secondsDisplay = seconds % 60;

    return `${hoursDisplay < 10 ? '0' : ''}${hoursDisplay}:${minutesDisplay < 10 ? '0' : ''}${minutesDisplay}:${secondsDisplay < 10 ? '0' : ''}${secondsDisplay}`;
  };

  const getTimerButtonClasses = () => {
    const timerButtonClasses = pause ? 'bg-dkGreen' : 'bg-dkRed';
    return timerButtonClasses;
  }
  const getTimerClasses = () => {
    const timerClasses = (timeLeft < 120) ? 'blinking-fade' : '';
    return timerClasses;
  }

  return (
    <div className='containerBox flexContainer size20 bold'>
      <div className={`flex3Column r-10 pt-10 pb-10 button color-lite ${getTimerButtonClasses()}`} onClick={(pause) ? (timeLeft == 0) ? resetTimer : startTimer : pauseTimer}>
        {!pause ? 'PAUSE' : (isStart()) ? 'START' : (timeLeft == 0)?'RESTART':'RESUME'}
      </div>
      <div className='ml-5 mr-5 flex3Column size40 r-10 bg-tinted pt-10 pb-10 color-yellow'>
        <span className={getTimerClasses()}>
          {formatTime(Number(timeLeft))}
        </span>
      </div>
      <div className={`flex3Column r-10 pt-10 pb-10 color-lite button ${getTimerButtonClasses()}`} onClick={resetTimer}>
        RESET
      </div>
    </div>
  );
};

export default CountdownTimer;