import React, { useState, useEffect, useContext } from 'react';
import Sounds from '../utils/Sounds';
import initCircuitTracking from './initCircuitTracking';
import initializeData from './initializeData';
import { CircuitContext } from '../context/CircuitContext';

const CircuitTimer = ({
    category,
    excersize,
    index,
    groupIndex,
    subgroupIndex,
    //circuits,
    time,
    toggleCheckbox,
    //activated,
    triggerNextTimer,
    //setActivate
}) => {

    const {
        circuits,
        setCircuits,
        activeIndex,
        setActiveIndex,
        nextActiveIndex,
        setNextActiveIndex,
        activated,
        setActivated,
        ticker,
        setTicker,
        countdown,
        setCountdown,
        breathing,
        setBreathing
    } = useContext(CircuitContext);

    const getInitTime = () => {
        //const localData = JSON.parse(localStorage.getItem('circuitTracking'));
        const localData = [...circuits];
        const currentLocalExcersize = localData[groupIndex].circuits[subgroupIndex].excersizes[index];
        if (category === 'session') {
            return Number(currentLocalExcersize.currentTime) || time;
        } else {
            return Number(currentLocalExcersize.restTime) || time;
        }
    }
    const [pause, setPause] = useState((excersize.activated) ? false : true);
    const [startTime, setStartTime] = useState(excersize.startTime);
    //const [currentTime, setCurrentTime] = useState(getRecordedCurrentTime());
    const [currentTime, setCurrentTime] = useState(getInitTime());

    const isStart = () => (currentTime == time) ? true : false;
    const totalSeconds = (milliseconds) => Math.floor(milliseconds / 1000);
    const id = `${category}index${index}groupIndex${groupIndex}subgroupIndex${subgroupIndex}`;

    const calculateTimeLeft = () => {

        //alert(`calculateTimeLeft`)
        const millisecondsRemaining = currentTime * 1000;
        const elapsedMilliseconds = Number(Date.now() - startTime);
        const remainingMilliseconds = Number(millisecondsRemaining - elapsedMilliseconds);
        const timeRemaining = totalSeconds(remainingMilliseconds);
        const secondsRemaining = (timeRemaining > 0) ? timeRemaining : 0;
        if (secondsRemaining <= 5 && secondsRemaining > 0 && countdown) {
            //Sounds.boop(0, 1);
            //Sounds.boop();
            Sounds.clank();
        } else if (secondsRemaining > 5 && ticker) {
            //Sounds.clank();
            Sounds.softBell(.95);
        }
        setCurrentTime(timeRemaining);
        if (timeRemaining <= 0) {            
            setPause(true);
            toggleCheckbox(index, groupIndex, subgroupIndex);
            setTimeout(() => {
                triggerNextTimer(index, groupIndex, subgroupIndex)
            }, 1000);
            Sounds.drip();
        }
    };

    const startTimer = () => {
        //alert('start')
        setStartTime(Date.now());
        //const updatedCircuits = [...circuits];
        //updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].activated = true;
        //updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].currentTime = currentTime;
        //updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].startTime = startTime;
        //localStorage.setItem('circuitTracking', JSON.stringify(updatedCircuits));
        //setCircuits(updatedCircuits);
        //setActivated(true);
        setPause(false);
    };
    const startManualTimer = () => {
        //alert(`startManualTimer: ${id}`)
        setActivated(true);
        setActiveIndex(id);
        setNextActiveIndex(id);
        startTimer();
    }

    const pauseTimer = () => {
        const updatedCircuits = [...circuits];
        updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].activated = false;
        if (category === 'session') {
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].currentTime = currentTime;
        } else {
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].restTime = currentTime;
        }
        //setCircuits(updatedCircuits);
        localStorage.setItem('circuitTracking', JSON.stringify(updatedCircuits));
        setActivated(false);
        setPause(true);
    };

    const resetTimer = () => {
        setPause(true);
        setStartTime(0);
        setCurrentTime((excersize.type === 'track') ? 0 : time);
    };
    /*
    useEffect(() => {
        resetTimer();
    }, [time]);
    */
    useEffect(() => {
        localStorage.setItem('activated', activated);
    }, [activated]);

    useEffect(() => {
        let timerInterval;
        if (!pause) {
            calculateTimeLeft();
            timerInterval = setInterval(() => {
                calculateTimeLeft();
            }, 1000);
        }
        return () => clearInterval(timerInterval);
    }, [pause/*, startTime*/]);
    /*
        useEffect(() => {
            console.log(`CircuitTimer => time: ${time} circuits: ${JSON.stringify(circuits)}`);
            const updatedCircuits = [...circuits];
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].activated = !pause;
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].currentTime = currentTime;
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].startTime = startTime;
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes[index].time = time;
            updatedCircuits[groupIndex].circuits[subgroupIndex].excersizes.map((excersize) => excersize.time = time);
            setCircuits(updatedCircuits);
            setActivate(!pause, subgroupIndex);
        }, [pause, currentTime, startTime]);
    */
    useEffect(() => {
        //console.log(`CircuitTimer => ${excersize.title} activated: ${activated}`);
        //console.log(`CircuitTimer => ${excersize.title} id: ${id}`);
        //console.log(`CircuitTimer => ${excersize.title} activeIndex: ${activeIndex}`);
        localStorage.setItem('time', time);
        //setLocalCircuits(JSON.parse(localStorage.getItem('circuitTracking')));
        //const updatedCircuits = JSON.parse(localStorage.getItem('circuitTracking'));
        const localActivated = localStorage.getItem('activated');
        if (activeIndex == id && localActivated === 'true') {
            startTimer();
        }
    }, []);
    const formatTime = (seconds) => {
        if (activeIndex == id) {
            localStorage.setItem('activeCircuitTime', seconds);
        }
        const hoursDisplay = Math.floor(seconds / 3600);
        const minutesDisplay = Math.floor((seconds % 3600) / 60);
        const secondsDisplay = seconds % 60;
        return `${hoursDisplay < 10 ? '0' : ''}${hoursDisplay}:${minutesDisplay < 10 ? '0' : ''}${minutesDisplay}:${secondsDisplay < 10 ? '0' : ''}${secondsDisplay}`;
    };

    const getTimerButtonClasses = () => {
        const timerButtonClasses = pause ? 'bg-dkGreen' : 'bg-dkRed';
        return timerButtonClasses;
    }

    return (
        <div id={id} className='flexContainer bold'>
            <div className={`containerBox flex3Column button color-lite contentCenter ${getTimerButtonClasses()}`} onClick={(pause) ? ((excersize.type === 'timer') && (currentTime == 0)) ? resetTimer : startManualTimer : pauseTimer}>
                {!pause ? 'PAUSE' : (isStart()) ? 'START' : (currentTime == 0) ? 'RESTART' : 'RESUME'}
            </div>
            <div className='containerDetail flex3Column bg-tinted color-yellow contentCenter size25 pt-10 mt-5 mb-5'>
                {formatTime(Number(currentTime))}
            </div>
            <div className={`containerBox flex3Column color-lite button contentCenter ${getTimerButtonClasses()}`} onClick={resetTimer}>
                RESET
            </div>
        </div>
    );
};

export default CircuitTimer;