import React, { useEffect, useState, useContext } from 'react';
import BreathingPath from './BreathingPath';
import Sounds from './Sounds';
import icons from './icons';
import { CircuitContext } from '../context/CircuitContext';

const Breathing = () => {

    const {
        circuits,
        setCircuits,
        activeIndex,
        setActiveIndex,
        activated,
        setActivated,
        ticker,
        setTicker,
        countdown,
        setCountdown,
        breathing,
        setBreathing
    } = useContext(CircuitContext);

    const [seconds, setSeconds] = useState(5);
    const [index, setIndex] = useState(0);
    const instructions = ['inhale', 'hold', 'exhale', ''];
    const soundEffects = [Sounds.tuningUp, '', Sounds.tuningDown, ''];
    const times = [5, 5, 9, 0];

    const [timer, setTimer] = useState(0);

    useEffect(() => {
            const intervalId = setInterval(() => {
                setTimer((prevTimer) => prevTimer + 1);
            }, 1000);
            return () => clearInterval(intervalId);
    }, []);
    useEffect(() => {
            if (timer >= (times[index])) {
                setTimeout(() => {
                    setTimer(0);
                    if (index == 3) {
                        setIndex(0);
                    } else {
                        setIndex((prevTimer) => prevTimer + 1);
                    }
                }, 1); // Wait for 1 second before resetting
            }
    }, [timer]);
    useEffect(() => {
        if (breathing) {
            //Sounds[soundEffects[index??0]]();
            console.log(`Breathing => ${index}`);
            if ((index !== 1) && (index !== 3)) {
                soundEffects[index]();
            }
        }
    }, [index]);
    return (
        <div>
            <div className='ball-container'>
                <div className='ball box-shadow pt-15 size12 bold color-dark'>
                    {instructions[index]}
                </div>
            </div>
            <div className='containerBox button' onClick={()=>(breathing)?setBreathing(false):setBreathing(true)}>{(breathing)?icons.soundOn:icons.soundOff}</div>
        </div>
    );
};

export default Breathing;