import React, { useState, useEffect } from 'react';
import axios from 'axios';
import config from '../apis/config';
//import DropDown from '../utils/DropDown';
import DropDown from '../forms/DropDown';

const Translator = () => {
  const [inputText, setInputText] = useState('');
  const [selectedLanguage, setSelectedLanguage] = useState('en'); // Default language is English
  const [translatedText, setTranslatedText] = useState('');
  const [loading, setLoading] = useState(false);

  const translateText = async () => {
    setLoading(true);
    try {
      const response = await axios.post(
        'https://translation.googleapis.com/language/translate/v2',
        null,
        {
          params: {
            q: inputText,
            target: selectedLanguage,
            key: config.googleAPI_KEY,
          },
        }
      );
      setTranslatedText(response.data.data.translations[0].translatedText);
      setLoading(false);
    } catch (error) {
      console.error('Error translating text:', error);
      setLoading(false);
    }
  };

  useEffect(() => {
    console.log(`Translator => selectedLanguage: ${JSON.stringify(selectedLanguage, null, 2)}`);
    const delayDebounceFn = setTimeout(() => {
      translateText();
    }, 1000);

    return () => clearTimeout(delayDebounceFn);
  }, [inputText, selectedLanguage]);

  const options = [
    { label: 'Spanish', value: 'es' },
    { label: 'English', value: 'en' },
    { label: 'French', value: 'fr' },
    { label: 'Hindi', value: 'hi' },
    { label: 'Hawaiian', value: 'haw' },
    { label: 'Indonesian', value: 'id' },
    { label: 'Italian', value: 'it' },
    { label: 'Japanese', value: 'ja' },
    { label: 'Javanese', value: 'jv' },
    { label: 'Tagalog', value: 'tl' }
  ];

  const selectLanguage = (event) => {
    console.log(`Translator => selectLanguage: ${event.target.value}`)
    setSelectedLanguage(event.target.value);
  };

  return (
    <div className='containerBox'>
      <div className='containerBox p-20'>
        <input
          className='containerBox p-20'
          type="text"
          value={inputText}
          onChange={(e) => setInputText(e.target.value)}
          placeholder="Enter text to translate"
          style={{ width: '100%', marginBottom: '10px' }}
        />
        {/* 
        <select
          value={selectedLanguage}
          onChange={(e) => setSelectedLanguage(e.target.value)}
          style={{ width: '100%', marginBottom: '10px' }}
        >
          <option value="en">English</option>
          <option value="es">Spanish</option>
          <option value="fr">French</option>
          Add more language options as needed 
          
        </select>
        */}
        <div className=''>
          {/*
          <DropDown
            label='Select a language:'
            options={options}
            selected={selectedLanguage}
            onSelectionChange={setSelectedLanguage}
            classes='containerBox p-20'
          />
          */}
          <DropDown
            value={selectedLanguage}
            options={options}
            onChange={selectLanguage}
            classes='containerBox width-100-percent p-20 bg-lite'
          />
        </div>
        <div className='containerBox mt-20 p-20 size30 p-20 contentLeft'>
          {
            (loading)
              ? 'Loading...'
              : translatedText
          }
        </div>
      </div>
    </div>
  );
};

export default Translator;
