import React, { useState, useEffect } from 'react';
import WindSelector from './wind/WindSelector.js';
import TideSelector from './tide/TideSelector.js';
import SwellSelector from './SwellSelector.js';
import ConditionsContext from '../context/ConditionsContext.js';
import Selector from '../forms/FunctionalSelector.js';
import CollapseToggleButton from '../utils/CollapseToggleButton';
import icons from '../utils/icons.js';

const  ConditionsSelectors = ({
    status,
    setWindStatus,
    setTide,
    setWind,
    handleWindCheck,
    handleTideCheck,
    handleTideSelection,
    handleSwellCheck,
    handleSwell1Selection,
    handleSwell2Selection,
    handleSwell1Angle,
    handleSwell2Angle,
    handleSwell1Height,
    handleSwell2Height,
    handleSwell1Interval,
    handleSwell2Interval,
    handleStarSelection,
    handleDistanceSelection,
    pause,
    setStatus,
    tideNow,
    data,
    tideDisplay
    
}) => {

    const context = ConditionsContext;
    const refresh = () => window.location.pathname = '/reactor/Waves';
    
    const getLocalData = (localItem) => localStorage.getItem(localItem);
    const collapseStateInit = (localItem) => getLocalData(localItem) ? getLocalData(localItem) === 'true' : true;
    const [conditionsCollapse, setConditionsCollapse] = useState(collapseStateInit('conditionsCollapse'));
    
    useEffect(() => {
        localStorage.setItem('conditionsCollapse', conditionsCollapse);
    }, [conditionsCollapse]);

    const starSelector = (stars) => <div className='containerBox flex2Column mt-5' >
                                    <div className='containerBox bg-lite'>
                                        Match
                                    </div>
                                    <div className='size20 width-auto mr-10'>
                                        <Selector
                                            groupTitle='Matches' 
                                            selected={stars} 
                                            label='Quality'
                                            items={[0,1,2,3,4,5]}
                                            onChange={handleStarSelection}
                                            fontSize='20'
                                            padding='5px'
                                            width='93%'
                                        />
                                    </div>
                                </div>
    const milesInput = (distance) => <div className='containerBox flex2Column mt-5'>
                                        <label>
                                            <div className='containerBox bg-lite'>
                                                Miles
                                            </div>
                                            <input className='containerBox width--10'
                                                name='distance'
                                                type='number'
                                                value={(distance=== '' ? 10 : distance)}
                                                onChange={handleDistanceSelection}
                                            />
                                        </label>
                                    </div>
    
    return <div>
                <div className='containerBox bold color-yellow bg-lite p-20'>
                    <CollapseToggleButton
                        title={`${icons.save} SELECT CONDITIONS`}
                        isCollapsed={conditionsCollapse}
                        setCollapse={setConditionsCollapse}
                        align='left'
                    />
                </div>
                {
                    (conditionsCollapse) 
                    ? <div></div> 
                    : <div className='containerBox'>
                        <div className='flexContainer'>
                            <SwellSelector 
                                id='1' 
                                //swellDirection={context.swell1Direction} 
                                swellDirection={status.swell1Direction} 
                                //status={context} 
                                status={status}
                                handleSwell1Selection={handleSwell1Selection} 
                                handleSwell2Selection={handleSwell2Selection} 
                                handleSwell1Angle={handleSwell1Angle} 
                                handleSwell2Angle={handleSwell2Angle} 
                                handleSwell1Height={handleSwell1Height} 
                                handleSwell2Height={handleSwell2Height} 
                                handleSwell1Interval={handleSwell1Interval} 
                                handleSwell2Interval={handleSwell2Interval} 
                                handleSwellCheck={handleSwellCheck}  
                                pause={pause}>
                            </SwellSelector>
                            <SwellSelector 
                                id='2' 
                                //swellDirection={context.swell2Direction} 
                                //status={context} 
                                swellDirection={status.swell2Direction} 
                                status={status} 
                                handleSwell1Selection={handleSwell1Selection} 
                                handleSwell2Selection={handleSwell2Selection} 
                                handleSwell1Angle={handleSwell1Angle} 
                                handleSwell2Angle={handleSwell2Angle} 
                                handleSwell1Height={handleSwell1Height} 
                                handleSwell2Height={handleSwell2Height} 
                                handleSwell1Interval={handleSwell1Interval} 
                                handleSwell2Interval={handleSwell2Interval} 
                                handleSwellCheck={handleSwellCheck} 
                                pause={pause}>
                            </SwellSelector>
                        </div>
                        <div className='flexContainer'>
                            <TideSelector 
                                tideNow={tideNow} 
                                data={data} 
                                //status={context}
                                status={status}
                                pause={pause} 
                                tideDisplay={tideDisplay} 
                                handleTideCheck={handleTideCheck} 
                                handleTideSelection={handleTideSelection}
                            />
                            <WindSelector 
                                status={status}
                                //windDirection={context.windDirection} 
                                windDirection={status.windDirection} 
                                pause={pause} 
                                setWind={setWind} 
                                //isWind={context.isWind} 
                                isWind={status.isWind} 
                                setStatus={setWindStatus} 
                                handleWindCheck={handleWindCheck}
                            />
                        </div>
                        <div className='flexContainer'>
                            {/*milesInput(context.distance)*/}
                            {/*starSelector(context.stars)*/} 
                            {milesInput(status.distance)}
                            {starSelector(status.stars)} 
                        </div>
                    </div>
                }
                <div className='button bg-neogreen r-10 m-5 p-15 color-black bold' onClick={refresh}>
                    Refresh
                </div>
            </div>    
}
export default ConditionsSelectors;
