const getKey = (label) => {
    const timeKey = Number(String(Date.now()).substring(7,13));
    const firstRandomKey = Math.random()*100;
    const secondRandomKey = Math.random()*100;
    const thirdRandomKey = Math.random()*100;
    const key = `${label}${timeKey}${firstRandomKey}${secondRandomKey.toFixed(3)}${thirdRandomKey}`;
    //console.log(`key: ${key}`);
    return key            
}
export default getKey;  