import React, {useState, useEffect} from 'react';
import LogEntry from './LogEntryFunctional';
import Loader from '../site/Loader';
import interfaceData from './InterfaceData'
import templateData from './LogTemplateData';
import generateNewLogId from './GenerateLogId';
import PostDirectory from './PostDirectory';

const Session = ({logId, location}) => { 

    const [
        postDirectory, 
        setPostDirectory,
        getPost, 
        getLastIndex,
        getLastPost, 
        addPost,
        savePost, 
        deletePost, 
        deleteLast
    ] = PostDirectory();
    
    console.log(`Session => logId: ${logId}\nlocalStorage.getItem('logId'): ${localStorage.getItem('logId')}`); 
    const logExists = () => {
        const available = (localStorage.getItem('logId') === null) ? false : true;
        console.log(`Session => logExists() available: ${available}`);
        console.log(`Session => logExists() logId: '${logId}' Local.logId: ${localStorage.getItem('logId')}`);
        //localStorage.getItem(logId)
        return available;
    }
    // eslint-disable-next-line
    const initLogId = () => (logExists()) ? logId : generateNewLogId();
    const initLog = () => (logExists() && logId !== '') ? (localStorage.getItem(logId)) ? JSON.parse(localStorage.getItem(logId)) : getLastPost() : templateData;
    console.log(`Session => initLog: ${JSON.stringify(initLog(),null,2)}`)
    const [status, setStatus] = useState({
        logID: initLogId(),
        //logID: localStorage.getItem('logId'),
        //log: initLog(),
        log: getLastPost(),
        isLoaded: false,
        items: []
    })
    // eslint-disable-next-line
    const getSpot = () => localStorage.getItem('spot');

    useEffect(() => {
        // eslint-disable-next-line
        const { state } = location;
        const logId = localStorage.getItem('logId');
        //console.log(`Session => componentDidMount -> SPOT: ${getSpot()}\ngetLogId: ${localStorage.getItem('logId')}\nlogId: ${logId}`); 
        
        if (localStorage.getItem(logId) === null) {
            const lastPost = getLastPost();
            console.log(`Session => useEffect => lastPost: ${lastPost}`)
            setStatus(prevState => ({
                ...prevState,
                //log: templateData,
                log: ((lastPost===0)||(lastPost===null))?initLog():lastPost,
                logID:generateNewLogId(),
                items: interfaceData,
                isLoaded: true
            }));
            //console.log(`Session => componentDidMount -> 1\nSPOT: ${getSpot()}\nlogID: ${logId}\nlog: ${JSON.stringify(status.log, null, 2)}`); 
        
        } else {
            const localLog = JSON.parse(localStorage.getItem(logId));
            console.log(`Session => useEffect => localLog: ${JSON.stringify(localLog,null,2)}`)
            setStatus(prevState => ({
                ...prevState,
                log: localLog ,
                logID: logId,
                items: interfaceData,
                isLoaded: true
            }));
            //console.log(`Session => componentDidMount -> 2\nSPOT: ${getSpot()}\nlogID: ${logId}\nlog: ${JSON.stringify(status.log, null, 2)}`); 
        }
        
        // eslint-disable-next-line
        /*
        const logData = JSON.parse(localStorage.getItem(logId)) || interfaceData;
        setStatus({
            log: logData,
            logID: logId,
            items: interfaceData,
            isLoaded: true
        });
        */
    }, []);

    useEffect(() => {
       console.log(`Session => useEffect => status.log: ${JSON.stringify(status.log,null,2)}`)
    }, [status]);

    const updateLog = (groupTitle, label, selected, set) => {
        const log = (status.log !== null) ? status.log : templateData;
        console.log(`updateLog => \ngroupTitle: ${groupTitle} \nlabel: ${label}\nselected: ${selected}\nset: ${set}\nlog: ${JSON.stringify(log, null, 2)}`)
        //log[groupTitle][label] = selected;
        if (groupTitle !== undefined && groupTitle !== 1 && selected !== undefined && set) {
            setStatus(prevState => ({
                ...prevState,
                log: log
            }));
        }
    }
    const getStateLog = () => {
        const log = status.log;
        console.log(`Session => getStateLog => log: ${log}`)
        return log;
    }
          
    //console.log(`Session ===> ${JSON.stringify(status.items, null, 2)}`);
    let appInterface = <div className='App-content pb-400 sizeMobile fadeIn'>
                            <div className='flex3Column'></div>
                            <div className='flex3Column'>
                                <Loader />
                            </div>
                            <div className='flex3Column'></div>
                        </div>;
                        
    if (status.isLoaded) {
        console.log(`Session => status.logID: ${status.logID}`)
        appInterface = <div className='fadeIn mt--40 mb-20 bg-lite pt-10 pb-20'>
            <LogEntry
                logId={status.logID}
                onChange={updateLog} 
                getStateLog={getStateLog} 
                title='Session Log' 
                message='Add your session data'
                items={status.items}
            />
        </div>
        
    }
    return (
        appInterface            
    )
    
}
export default Session;