
import getKey from '../utils/KeyGenerator';
import SurfScoringLogic from './SurfScoringLogic';
//import getPriorityColor from './GetPriorityColor';
import jerseyColors from './JerseyColors';
import getMedal from './GetMedal';

const getAthleteScore = (players, index, timesUp) => {
    //console.log(`getAthleteScore => index: ${index} players: ${players.length}`)
    return <div className={`flex${players.length}Column r-10 m-1 size20 bg-darker pt-10 pb-10`} key={getKey(`athleteScore`)}>
                <div className={`p-5 size30`}>{index+1}</div>
                <div className={`m-10 ht-5 bg-${jerseyColors[players[index].surfJerseyColor]}`}></div>
                <div className='p-5 ht-75 centeredContent'>
                    <div>
                        {getMedal(index)}
                    </div>
                    <div>{players[index].name.split(' ')[0]}</div>
                    <div>{players[index].name.split(' ')[1]}</div>
                </div>
                <div className='p-5 bg-lite r-10 m-10'>{players[index].surfScore}</div>
                <div className='greet'>
                    <SurfScoringLogic
                        index={index} 
                        completed={timesUp}
                        players={players}
                        //setPlayers={setPlayers}
                        //editPlayer={editPlayer}
                        //deleteAthlete={deletePlayer}
                        />
                </div>
            </div>
}
export default getAthleteScore;