import React, { useState, useEffect, useContext } from 'react';
import CircuitGroup from './CircuitGroup';
import getKey from './KeyGenerator';
import CollapseToggleButton from './CollapseToggleButton';
import { CircuitContext } from '../context/CircuitContext';
import initCircuitTracking from './initCircuitTracking';
import initializeData from './initializeData';
import icons from './icons';
import YouTubePlayer from './YouTubePlayer';
import VideoDetail from './VideoDetail';

const TrackCircuit = ({
}) => {
    
    const { 
        circuits, 
        setCircuits, 
        sort, 
        setSort,
        activeIndex,
        setActiveIndex,
        setActivated,
        ticker,
        setTicker,
        countdown,
        setCountdown,
        breathing,
        setBreathing,
        scrollToElement,
        videoId,
        setVideoId,
        videoActive,
        setVideoActive
    } = useContext(CircuitContext);

    const [, forceUpdate] = useState();
    const [collapse, setCollapse] = useState(false);
    
    //console.log(`TrackCircuits => circuits: ${JSON.stringify(circuits,null,2)}`)
    const notNull = (value) => (value !== null) ? true : false;
    const notEmpty = (value) => (value !== "") ? true : false;
    const isGood = (value) => (notNull(value) && notEmpty(value)) ? true : false;
    const localData = () => initializeData('circuitTracking', initCircuitTracking);

    useEffect(() => {
        console.log(`TrackCircuit => circuits update`);
        forceUpdate(Math.random());
    }, [circuits]);
    useEffect(() => {
        console.log(`TrackCircuit => videoActive: ${videoActive} videoId: ${videoId}`);
    }, [videoId]);
    useEffect(() => {
        const localData = initializeData('circuitTracking', initCircuitTracking);
        localData.map((circuitGroup, circuitGroupIndex) => {
            circuitGroup.circuits.map((circuit, circuitIndex) => {
                circuit.isCollapsed = collapse;
            });
        });
        setCircuits(localData);
    }, [collapse]);

    const addCircuit = (circuitGroupIndex, circuitIndex) => {
        //const updatedCircuits = [...circuits];
        const updatedCircuits = [...localData()];
        const circuit = prompt('Circuit name:', '');
        //const circuitDescription = prompt('Circuit description:', '');
        const description =  'Set the Timer: Determine the duration for each exercise, usually around 30 seconds to 1 minute. Arrange the exercises in a sequence that allows you to transition smoothly from one exercise to the next. Consider alternating between upper body and lower body exercises to give specific muscle groups time to rest.';
        
        const newCircuit = {
            title: circuit,
            description: description,
            //circuit: circuit
            //circuit: '',
            time: 5,
            restTime: 5,
            excersizes: [],
            isCollapsed: false
        }
        if (isGood(circuit)/* || isGood(circuit)*/) {
            updatedCircuits[circuitGroupIndex].circuits.push(newCircuit)
            setCircuits(updatedCircuits);
        }

    };
    const subtractCircuit = (circuitGroupIndex, circuitIndex) => {
        //const updatedCircuits = [...circuits];
        const updatedCircuits = [...localData()];
        const circuit = updatedCircuits[circuitGroupIndex].circuits[circuitIndex];
        circuit.sessions = circuit.sessions ?? [];
        circuit.isRunning = false;
        circuit.sessions.pop();
        setCircuits(updatedCircuits);
    };

    const deleteGroup = (circuitGroupIndex) => {
        //const toggle = window.confirm(`Are you sure you want to remove circuit group ${circuits[circuitGroupIndex].title}`)
        const toggle = window.confirm(`Are you sure you want to remove circuit group ${localData()[circuitGroupIndex].title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            //const updatedCircuits = [...circuits];
            const updatedCircuits = [...localData()];
            removeItemByIndex(updatedCircuits, circuitGroupIndex);
            setCircuits(updatedCircuits);
        }
    }
    const closePlayer = () => {
        setVideoActive(false);
        setVideoId(null);
    }
    const getVideoDetail = () => {
        scrollToElement(activeIndex);
        return {
            kind: "youtube#searchResult",
            etag: "yuJ8sfOwcgHU2dHV22aAbrF-5RY",
            id: {
                kind: "youtube#video",
                videoId: `${videoId}?autoplay=1`
            },
            snippet: {
                publishedAt: "2024-02-11T23:48:07Z",
                channelId: "UCTAkZ_TUCLSRjzGa7FoBK5Q",
                title: '',
                description: '',
                thumbnails: {
                    default: {
                        url: "https://i.ytimg.com/vi/F2njH0EXhSU/default.jpg",
                        width: 120,
                        height: 90
                    },
                    medium: {
                        url: "https://i.ytimg.com/vi/F2njH0EXhSU/mqdefault.jpg",
                        width: 320,
                        height: 180
                    },
                    high: {
                        url: "https://i.ytimg.com/vi/F2njH0EXhSU/hqdefault.jpg",
                        width: 480,
                        height: 360
                    }
                },
                channelTitle: '',
                liveBroadcastContent: "none",
                publishTime: "2024-02-11T23:48:07Z"
            }
        }
    }
    const reset = () => {
        const localData = initializeData('circuitTracking', initCircuitTracking);
        console.log(`localData: ${JSON.stringify(localData,null,2)}`);
        localData.map((circuitGroup, circuitGroupIndex) => {
            circuitGroup.circuits.map((circuit, circuitIndex) => {
                circuit.excersizes.map((excersize, excersizeIndex)=>{
                    excersize.complete = false;
                    excersize.activated = false;
                    excersize.currentTime = circuit.time;
                    excersize.restTime = circuit.restTime;
                    excersize.elapsedTime = 0;
                })
            })
        });
        setCircuits(localData);
        setActivated(false);
        setActiveIndex(null)
    }
    const tickerToggleButton = () => {
        return <div 
                className='containerBox bg-lite centerVertical p-20 color-yellow button scrollSnapRight' 
                onClick={()=>setTicker(!ticker)}
            >
                {icons.track} {(ticker)?icons.soundOn:icons.soundOff}
            </div>
    }
    const countdownToggleButton = () => {
        return <div 
                    className='containerBox bg-lite centerVertical p-20 color-yellow button scrollSnapRight' 
                    onClick={()=>setCountdown(!countdown)}
                >
                    {icons.alarmOn} {(countdown)?icons.soundOn:icons.soundOff}
                </div>
    }
    const resetButton = () => {
        return (
            <div onClick={() => reset()} className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'>
                Reset
            </div>
        )
    }
    const collapseButton = () => {
        return (
            <div onClick={() => setCollapse(!collapse)} className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'>
                {(collapse)?'Expand All':'Collapse All'}
            </div>
        )
    }
    const jumpToActive = () => {
        scrollToElement(activeIndex);
    }
    const jumpToActiveButton = () => {
        return (
            <div onClick={() => jumpToActive()} className='containerDetail m-5 bg-lite centerVertical p-20 size12 bold button scrollSnapRight'>
                Current Excersize
            </div>
        )
    }
    return (
        <div key={getKey('circuitGroupContainer')} className='pb-100'>
            <div className='containerBox bg-lite centerVertical p-20 color-yellow bold size25'>
                <CollapseToggleButton
                    title={'sort'}
                    isCollapsed={sort}
                    setCollapse={setSort}
                    align='center'
                />
            </div>
            {
                (sort)
                //? circuits.map((circuitGroup, circuitGroupIndex, array) => (
                ? localData().map((circuitGroup, circuitGroupIndex, array) => (
                    <div key={getKey('circuitGroups')}>
                        <CircuitGroup
                            //circuits={circuits}
                            //setCircuits={setCircuits}
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={(array.length - 1 - circuitGroupIndex)}
                            deleteGroup={deleteGroup}
                            addCircuit={addCircuit}
                            //setScroll={setScroll}
                            //targetElementRef={targetElementRef}
                            //scrollToBottom={scrollToBottom}
                            //sort={sort}
                        />
                    </div>
                ))
                //: circuits.map((circuitGroup, circuitGroupIndex) => (
                : localData().map((circuitGroup, circuitGroupIndex) => (
                    <div key={getKey('circuitGroups')}>
                        <CircuitGroup
                            //circuits={circuits}
                            //setCircuits={setCircuits}
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={circuitGroupIndex}
                            deleteGroup={deleteGroup}
                            addCircuit={addCircuit}
                            //setScroll={setScroll}
                            //targetElementRef={targetElementRef}
                            //scrollToBottom={scrollToBottom}
                        />
                    </div>
                ))
            }
            <div className={`containerDetail bt-5 ml-5 width--10 bg-tintedMediumDark size12 color-lite`}>
                <div className={`button-container`}>
                    <div className='scrollSnapRight'>
                        {jumpToActiveButton()}
                    </div>
                    <div className='scrollSnapRight'>
                        {collapseButton()}
                    </div>
                    <div className='scrollSnapRight mr-10'>
                        {resetButton()}
                    </div>
                    {tickerToggleButton()}
                    {countdownToggleButton()}
                </div>
            </div>
        </div>
    )
}

export default React.memo(TrackCircuit)