import React, { useEffect, useRef } from 'react';

const BreathingPath = () => {

    const canvasRef = useRef(null);
    const width = 300;
    const height = 200;
    const ballHeight = 50;
    const top = 45;
    const bottom = 195;

    useEffect(() => {
        const canvas = canvasRef.current;
        const ctx = canvas.getContext('2d');
        let startX = 540;

        const animate = () => {
            ctx.clearRect(0, 0, canvas.width, canvas.height);
            ctx.beginPath();
            ctx.moveTo((startX + 1140), top);
            ctx.lineTo((startX + 835), bottom);
            ctx.lineTo((startX + 780), bottom);
            ctx.lineTo((startX + 260), top);
            ctx.lineTo((startX - 60), top);
            ctx.lineTo((startX - 365), bottom);
            ctx.lineTo((startX - 420), bottom);
            ctx.lineTo((startX - 970), top);
            ctx.lineWidth = 5;
            ctx.strokeStyle = '#ffffff';
            ctx.stroke();
            startX-= 1.05
            //if (startX > 0) requestAnimationFrame(animate);
            if (startX <= (width-1000)) startX = 500; // Reset startX when it goes beyond the left edge
            requestAnimationFrame(animate);
        };

        animate();
    }, []);

    return <canvas ref={canvasRef} width={width} height={height} />;
};

export default BreathingPath;
