import React, { useState, useEffect } from 'react';
import initLinkTracking from './initLinkTracking';
import LinkGroup from './LinkGroup';
import getKey from './KeyGenerator';

const LinkSaver = ({
    links,
    setLinks,
    deleteGroup,
    isCollapsed
}) => {

    console.log(`links: ${JSON.stringify(links, null, 2)}`)
    useEffect(() => {
        localStorage.setItem('linkTracking', JSON.stringify(links));
    }, [links]);

    useEffect(() => {
        const storedLinks = localStorage.getItem('linkTracking');
        console.log(`storedLinks: ${storedLinks}`)
        if (storedLinks) {
            setLinks(JSON.parse(storedLinks));
        } else {
            setLinks(initLinkTracking);
        }
    }, []);

    const handleDeleteTask = (linkProjectIndex, linkIndex) => {
        const updatedLinks = [...links];
        updatedLinks[linkProjectIndex].links.splice(linkIndex, 1);
        setLinks(updatedLinks);
    };

    const addLink = (linkProjectIndex, linkIndex) => {
        const updatedLinks = [...links];
        const linkDescription = prompt('Link description:', '');
        const link = prompt('Link:', '');
        const newLink = {
            description: linkDescription,
            link: link
        }
        updatedLinks[linkProjectIndex].links.push(newLink)
        setLinks(updatedLinks);
    };
    const subtractLink = (linkProjectIndex, linkIndex) => {
        const updatedLinks = [...links];
        const link = updatedLinks[linkProjectIndex].links[linkIndex];
        link.sessions = link.sessions ?? [];
        link.isRunning = false;
        link.sessions.pop();
        setLinks(updatedLinks);
    };
    return (
        <div>
            {
                links.map((linkGroup, linkProjectIndex) => (
                    <div key={getKey(`linkGroup${linkProjectIndex}`)}>
                        <LinkGroup
                            linkGroup={linkGroup}
                            linkProjectIndex={linkProjectIndex}
                            addLink={addLink}
                            deleteGroup={deleteGroup}
                            isCollapsed={isCollapsed}
                            links={links}
                            setLinks={setLinks}
                        >
                        </LinkGroup>
                    </div>
                ))
            }
        </div>
    )
}

export default LinkSaver