import React, { useState, useEffect } from 'react';
import Sounds from './Sounds';
import getKey from './KeyGenerator';
import Selector from '../forms/FunctionalSelector';
import Breathing from './Breathing';
import BreathingPath from './BreathingPath';

const SoundBoard = () => {

   const [duration, setDuration] = useState();
   const times = [5, 10, 15, 20, 25, 30, 35, 40, 45, 50];

   const boardItems = [
    {
        title: 'Machine Gun',
        sound: 'machineGun'
    },
    {
        title: 'AK-47',
        sound: 'ak47'
    },
    {
        title: 'White Noise 1',
        sound: 'whiteNoise'
    },
    {
        title: 'White Noise 2',
        sound: 'water'
    },
    {
        title: 'Boop',
        sound: 'boop'
    },
    {
        title: 'Siren',
        sound: 'siren'
    },
    {
        title: 'Bell',
        sound: 'bell'
    },
    {
        title: 'Soft Bell',
        sound: 'softBell'
    },
    {
        title: 'Beep',
        sound: 'beep'
    },
    {
        title: 'Drip',
        sound: 'drip'
    },
    {
        title: 'Water',
        sound: 'water'
    },
    {
        title: 'Ping',
        sound: 'ping'
    },
    {
        title: 'Clank',
        sound: 'clank'
    },
    {
        title: 'Tuning Up',
        sound: 'tuningUp'
    },
    {
        title: 'Tuning Down',
        sound: 'tuningDown'
    },
    {
        title: 'Bamboo Wind Chime',
        sound: 'bambooWindChime'
    }
   ]
    useEffect(() => {
        setDuration(5);
    }, []);
    useEffect(() => {
        console.log(`Duration: ${duration}`);
    }, [duration]);

    const changeDuration = (dontCare, notConcerned, value) => setDuration(value);
    const playSound = (sound) => {
        console.log(`SoundBoard => ${sound} - Duration: ${duration}`)
        if (typeof Sounds[sound] === 'function') {
            Sounds[sound](duration); // Call the function dynamically using bracket notation
        } else {
            console.error(`Function ${sound} does not exist in Sounds`);
        }
    }
    return (
        <div>
            <div className='containerBox flexContainer centerVertical'>
                <div className='containerBox flex2Column color-yellow contentRight'>
                    Duration:
                </div>
                <div className='flex2Column color-yellow pr-10 contentLeft'>
                    <Selector
                        groupTitle='time'
                        label='Session time:'
                        items={times}
                        selected={duration}
                        onChange={changeDuration}
                        fontSize='25'
                        padding='10px'
                        width='100%'
                    />
                </div>
            </div>
            <div className='containerBox grid300'>
                {
                    boardItems.map((sound,index) => <div key={getKey(`${sound.title}${index}`)} className='containerBox flexColumn p-20 button' onClick={() => playSound(sound.sound)}>
                                                    {sound.title}
                                                </div>
                    )
                }
            </div>
            <div className='containerBox'>
                <div className='mb--200'>
                    <BreathingPath />
                </div>
                <div className='containerBox flexContainer'>
                    <div className='flex2Column'></div>
                    <div className='flexColumn'>
                        <Breathing />
                    </div>
                    <div className='flex2Column'></div>
                </div>
            </div>
        </div>
    );
};

export default SoundBoard;